.title{
    margin-top: 7em;
    display:flex;
    width:85%;
    @include responsive(m){
      width: 80%;
    }
    @include responsive(hd){
      width: 75%;
      margin-top: 10em;
    }

    &_number{
      font-size:34px;
      position: absolute;
      margin-left: 10px;
      @include responsive(m){
      font-size: 55px;
      }
      @include responsive(hd){
      font-size: 72px;
      }
    }

    &_name{
      display: flex;
      flex-direction: row;
      font-size:70px;
      width:100%;
      left:-65px;
      @include responsive(m){
      font-size: 110px;
      }
      @include responsive(hd){
      font-size: 144px;
      }
      
      &:before{
      content: "";
      width:50px;
      border-bottom: 2px solid $color2;
      margin: auto 10px auto 0;
      @include responsive(m){
      width:75px;
      }
      @include responsive(hd){
      width:100px;
      }
      }
      &:after{
      content: "";
      flex: 1 1;
      border-bottom: 2px solid $color2;
      margin: auto 0 auto 10px;
      }
    }
  }